.contact.section {
    padding-bottom: 6.25rem;
}

.contact__container {
    /* grid-template-columns: 4fr 8fr; */
    padding-top: 20vh;
    column-gap: 1.875rem;
}

.contact__title {
    position: relative;
    left: 25vw;
}


.contact__container .card {
    display: grid;
    position: relative;
    grid-template-columns: 4fr 4fr;
    column-gap: 1.875rem;
    left: 8vw;
    top: 10vh;
    padding-bottom: 15vh;
}

.contact__details{
    font-size: 1.5rem;
    box-shadow: var(--shadow);
    color: var(--text-color);
    height: fit-content;
    width: fit-content;
    border: none;
    outline: none;
    border-radius: 1.875rem;
    padding: 0.625rem 1.875rem;
    z-index: 1;
}

